import { Component, OnInit, OnDestroy, ChangeDetectionStrategy, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';


@Component({
  selector: 'app-clientloan-credit-line-options',
  templateUrl: 'loan-credit-line-options.component.html',
  styleUrls: ['loan-credit-line-options.component.scss'],
  providers: [],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ClientLoanCreditLineOptionsComponent implements OnInit, OnDestroy {
  creditLineParamsFG: FormGroup;

  @Input('data-source') productData: any;

  @Output('credit-line-params-get') creditLineParamsSend = new EventEmitter<any>();

  private _prepareParams = 0;
  @Input('credit-line-params-prepare') 
  public get prepareCreditLineParams(): number {
    return this._prepareParams;
  }
  public set prepareCreditLineParams(val) {
    this._prepareParams = val;
     
    if ( this._prepareParams !== 0 ) {
      this.creditLineParamsSend.emit(this.creditLineParamsFG.value);
    }    
  }
  constructor(
    private fb: FormBuilder,
  )  {
  }
  
  getInitCreditLineParams() {
    return {      
      revolvingCreditLine:   this.productData.revolvingCreditLine.toString(),
      drawnDownFixed: this.productData.drawnDownFixed.toString(),
      creditLineValueUtil:  this.productData.creditLineValueUtil
    }
  }

  ngOnInit() {    
    this.prepareCreditLineParams = 0;
    
    this.creditLineParamsFG = this.fb.group({
      revolvingCreditLine:    [''],
      drawnDownFixed:         [''],
      creditLineValueUtil:    ['']
    });

    this.creditLineParamsFG.patchValue(this.getInitCreditLineParams());
  // eof OnInit     
  }

  ngOnDestroy() {

  }

}
