import { Component, OnInit, OnDestroy, ChangeDetectionStrategy, Input } from '@angular/core';

import { ClientDetailProfileInterface, ClientDetailAddressInterface } from '../client-detail.interface';

@Component({
  selector: 'app-client-detail-personal-info',
  templateUrl: 'client-detail-personal-info.component.html',
  styleUrls: ['client-detail-personal-info.component.scss'],
  providers: [],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ClientDetailPersonalInfoComponent implements OnInit, OnDestroy {

  /*
   <app-client-detail-personal-info  [isVisible]="true"
       [id]="id" [profile]="profile" [loanOfficer]="loanOfficer"  [branch]="branch"
     ></app-client-detail-personal-info>

  */
  public isVisible = true;

  // @Input() clientId: number;

  @Input() id: number;
  @Input() profile: ClientDetailProfileInterface;
  
  @Input() loanOfficer: string;
  @Input() branch: string;

  constructor()  {
  }

  ngOnInit() {

  }

  getFullName(firstName: string, lastName: string, middleName: string) {

    let fullName = firstName + ' ' + lastName;
    if (middleName) {
      fullName = fullName + ' ' + middleName.trim();
    }
    return fullName;
  }

  getPhoneNumber(phoneNumber: string) {
    return phoneNumber;
  }

  getEmail(email: string) {
    return email;
  }

  isProfileImage(profileImage: string) {

     if (profileImage) {
       return true;
     }
     return false;
  }

  getProfileImageUrl(profileImage: string) {
    return profileImage;
  }


  getAddressTypes(addressTypes: string[]) {

    let types: string[] = [];

    for (let i = 0; i < addressTypes.length; i++) {
      
        types[i] = addressTypes[i];

        if (i < (addressTypes.length - 1) ) {
              types[i] += ','; 
        }
    }
       return types;
  }
 


  isAddressType(address: ClientDetailAddressInterface, addressType: string) {

    function isName(element, index, array) {
      if (element.trim().toUpperCase() === addressType.trim().toUpperCase()) {
        // console.log('isAddressType addressType -> %O   element  -> %O',  addressType, element);
        return true;
      }
      return false;
    }

    if (address && address.addressTypes && address.addressTypes.length > 0) {

      // console.log('isAddressType addressType -> %O   address.addressTypes  -> %O',  addressType, address.addressTypes);

      return address.addressTypes.find(isName);     
    }
 
 }

  ngOnDestroy() {
  }

}
