import { Inject, Injectable,InjectionToken } from '@angular/core';
import { HttpClient, HttpParams, HttpErrorResponse, HttpHeaders } from '@angular/common/http';

import { of, Subject } from 'rxjs';
import { map, catchError, takeUntil, shareReplay, startWith, delay } from 'rxjs/operators';


import { EntityStateService } from '../../../shared/services/entity-state.service';
import { IsEntityChangedInterface, EntityMessageTypeEnum, EntityStateEnum, EntityChangeResultEnum } from '../../../shared/model/entity-state.interface';

import { environment } from 'src/environments/environment';

import {ServerStatusService } from '../../../shared/services/server-status.service';

import { PaymentScheduleInterface, PaymentInterface, WrongPaymentSchedule, PaymentScheduleRequestsInterface } from './payment-schedule.Interface';

@Injectable({
  providedIn: 'root'
})
export class AmortizationScheduleService {
  private apiUrl = environment.apiUrl;

  private curPaymentScheduleSub$ = new Subject<any[] | PaymentScheduleInterface>();
  public curPaymentSchedule$ = this.curPaymentScheduleSub$.asObservable()
                        .pipe(
                          startWith(WrongPaymentSchedule),
                          shareReplay(1)
                        );
  public curPaymentSchedule: PaymentScheduleInterface = WrongPaymentSchedule;
  
  private onDestroySub$ = new Subject<boolean>();

  constructor(
    private http: HttpClient,
    private serverStatusSrv: ServerStatusService) {

      this.curPaymentSchedule$.subscribe(
        paymentSchedule => {
          this.curPaymentSchedule = paymentSchedule as PaymentScheduleInterface;
        }
      );
  }
  // ------------------------------------------------------------
  public isWrongPaymentSchedule(paymentSchedule: PaymentScheduleInterface): boolean {
    if (!paymentSchedule) {
      throw new Error('Parameter [paymentSchedule] is null');
    }
    if (paymentSchedule === WrongPaymentSchedule) {
      return true;
    } else {
        return false;
    }
  }  

  public isPaymentScheduleNotEmpty(paymentSchedule: PaymentScheduleInterface): boolean {
    if (!paymentSchedule) {
      throw new Error('Parameter [paymentSchedule] is null');
    }
    if (paymentSchedule === WrongPaymentSchedule) {
      return false;
    } else {
        return true;
    }
  }
  private isPaymentScheduleEmpty(): boolean {
    return !this.isPaymentScheduleNotEmpty(this.curPaymentSchedule);
  }
  public clearPaymentSchedule(): void {
    if (!this.isPaymentScheduleEmpty()) {
      this.curPaymentScheduleSub$.next(WrongPaymentSchedule);
    }
  }
// ------------------------------------------------------------
public loadPaymentSchedule(stateService: EntityStateService, productId: number, paymentScheduleRequestsInterface: PaymentScheduleRequestsInterface): void {
   console.log('SYNC ENTER PaymentScheduleService.loadPaymentSchedule() stateService -> %O   productId-> %O  paymentScheduleRequestsInterface -> %O', stateService, productId, paymentScheduleRequestsInterface);

  if (stateService) {
    const isClentsChangedEnter: IsEntityChangedInterface = {
      op: EntityStateEnum.LOAD,
      isEnd: false,
      opResult: EntityChangeResultEnum.NOSET
    };
    stateService.next(isClentsChangedEnter);
  }

  // test fake get !!! makarov

  this.http.post<PaymentScheduleInterface>(`${this.apiUrl}/products/${productId}/paymentschedulerequests`, paymentScheduleRequestsInterface, { 
  // this.http.get<any>(`${this.apiUrl}/products/${productId}`, {
    headers: new HttpHeaders()
      // .set(Content-Type", "application/json")
      .set("Accept", "application/json")
  })
  .pipe(
    map((paymentSchedule:  any ) => {
       console.log('\tASYNC PIPE PaymentScheduleService.loadPaymentSchedule().map(ANY) paymentSchedule -> %O', paymentSchedule);
        
       // Control as PaymentScheduleInterface !!!  ???

      if (this.isWrongPaymentSchedule(paymentSchedule)) {
        return null;
      }
    
      return {token: 'OK', paymentSchedule: paymentSchedule};
    }),
    catchError((err: any): any =>{
       console.log('\tASYNC PIPE PaymentScheduleService.loadPaymentSchedule().catchError() #2 -> %O', err);

      let msg: string;
      let isPaymentScheduleChangedError: IsEntityChangedInterface = null;
      if (stateService) {
        isPaymentScheduleChangedError = {
          op: EntityStateEnum.LOAD,
          isEnd: true,
          opResult: EntityChangeResultEnum.ERROR,
        };
      }
      if (err instanceof HttpErrorResponse) {
        // Server error 
        const status = err.status;
        if (status === 0) {
          msg = `SERVER ERROR: HTTP failure response for ${err.url}`;
        } else if (status > 0 && status < 600) {
            msg = `SERVER ERROR: ${status} - ${this.serverStatusSrv.getStatusText(status.toString())}`;
        }
        isPaymentScheduleChangedError.messageType = EntityMessageTypeEnum.ERROR;
        isPaymentScheduleChangedError.message = msg;
      } else {
        // paymentSchedule error 
        msg = err.message ? err.message : err.toString(); ;
        isPaymentScheduleChangedError.messageType = EntityMessageTypeEnum.ERROR;
        isPaymentScheduleChangedError.message = `PaymentSchedule ERROR: ${msg}`;
      }
      if (stateService) {
        stateService.next(isPaymentScheduleChangedError);
      }      
      this.clearPaymentSchedule();
      this.onDestroySub$.next(true);
      
      return of({token: 'ERROR', paymentSchedule: null});
    }),
    // delay(5000),                //  test makarov
    takeUntil(this.onDestroySub$)
  ).subscribe(
    (rezObj: {token: string; paymentSchedule: any} ) => {
       console.log(`\tSUBSCRIBE: PaymentScheduleService.loadPaymentSchedule().subscribe(NEXT): rezObj -> %O`, rezObj);

       if (rezObj.token === 'OK') {
         this.curPaymentScheduleSub$.next(rezObj.paymentSchedule); // BEFOR stateService.next()!

        if (stateService) {
          const isPaymentScheduleChangedExit: IsEntityChangedInterface = {
            op: EntityStateEnum.LOAD,
            isEnd: true,
            opResult: EntityChangeResultEnum.SUCCESS
          };
          stateService.next(isPaymentScheduleChangedExit);
        }
        // this.curPaymentScheduleSub$.next(rezObj.paymentSchedule);
      }
    }
    // , err => console.log(`\tSUBSCRIBE: PaymentScheduleService.loadPaymentSchedule().subscribe(ERROR): %O`, err)
    // , () => console.log(`\tSUBSCRIBE: PaymentScheduleService.loadPaymentSchedule().subscribe(COMPLETE)`)    
  );

}

}
