import { Component, OnInit, OnDestroy, ChangeDetectionStrategy } from '@angular/core';

import { Observable, Subject } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import {Subscription} from 'rxjs';

import { Router, ActivatedRoute, Params, ParamMap } from '@angular/router';

import { ClientDetailInterface } from './client-detail.interface';


@Component({
  selector: 'app-client-detail-documemts',
  templateUrl: './client-detail-documemts.component.html',
  styleUrls: ['./client-detail-documemts.component.scss'],
  providers: [
    // ClientDocumemtsStateService
  ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ClientDetailDocumemtsComponent implements OnInit, OnDestroy {

  activatedRouteParentParams$: Observable<string> = null;

  public clientId: string;

  client: ClientDetailInterface = null;

  constructor( private activatedRoute: ActivatedRoute,
               private router: Router ) {
  }

  ngOnInit() {

    this.activatedRouteParentParams$ = this.activatedRoute.parent.params
    .pipe(
      map((pm: Params) => pm.id),
      tap((clientId: string) => {
          console.log('ClientDocumemtsComponent activatedRoute.parent.params -> %o', clientId);
          this.clientId = clientId;
        })
    );
  }

  ngOnDestroy() {
    /*
    if (this.simpleSnackBarRef != null) {
      this.simpleSnackBarRef.dismiss();
      this.simpleSnackBarRef = null;
    }
    */
  }
}
