import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpErrorResponse, HttpHeaders } from '@angular/common/http';

import { of, Subject } from 'rxjs';
import { map, catchError, takeUntil, shareReplay, startWith } from 'rxjs/operators';

import { EntityStateService } from './entity-state.service';
import {
  IsEntityChangedInterface, EntityMessageTypeEnum,
  EntityStateEnum, EntityChangeResultEnum
} from '../model/entity-state.interface';
import { ServerStatusService } from './server-status.service';

import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class EntityCreditScoreInquiryService {
  private apiUrl = environment.apiUrl;

  private onDestroySub$ = new Subject<boolean>();

  constructor(
    private http: HttpClient,
    private serverStatusSrv: ServerStatusService
  ) { }


  public creditScoreInquiry(urlPath: string, body: any, 
                           stateService: EntityStateService): void {
    console.log('SYNC ENTER EntityCreditScoreInquiryService.creditScoreInquiry() body -> %O, stateService -> %O', body, stateService);

    if (stateService) {
      const isEntityChanged: IsEntityChangedInterface = {
        op: EntityStateEnum.UPDATE,
        isEnd: false,
        opResult: EntityChangeResultEnum.NOSET
      };
      stateService.next(isEntityChanged);
    }
    this.http.post<any>(`${this.apiUrl}${urlPath}`, body, {
      headers: new HttpHeaders()
        .set("Content-Type", "application/json")
        .set("Accept", "application/json")
    })
      .pipe(
        map((result: any) => {
          console.log('\tASYNC PIPE EntityCreditScoreInquiryService.creditScoreInquiry().map(ANY): %O', result);

          // if (this.isLoanProductNotFound(product)) {
          //   throw new Error(`Loan Product with [productId] = ${prodId} has not found.`);
          // }

          return { token: 'OK', result };
        }),
        catchError((err: any): any => {
          console.log('\tASYNC PIPE EntityCreditScoreInquiryService.creditScoreInquiry().catchError(): %O', err);

          let msg: string;
          let isEntityChangedError: IsEntityChangedInterface = null;
          if (stateService) {
            isEntityChangedError = {
              op: EntityStateEnum.UPDATE,
              isEnd: true,
              opResult: EntityChangeResultEnum.ERROR,
            };
          }
          if (err instanceof HttpErrorResponse) {
            // Server error 
            const status = err.status;
            if (status === 0) {
              msg = `SERVER ERROR: HTTP failure response for ${err.url}`;
            } else if (status > 0 && status < 600) {
               msg = `SERVER ERROR: ${status} - ${this.serverStatusSrv.getStatusText(status.toString())}`;
            }
            isEntityChangedError.messageType = EntityMessageTypeEnum.ERROR;
            isEntityChangedError.message = msg;
          } else {
            // request error 
            msg = err.message ? err.message : err.toString();;
            isEntityChangedError.messageType = EntityMessageTypeEnum.ERROR;
            isEntityChangedError.message = `ERROR: ${msg}`;
          }
          if (stateService) {
            stateService.next(isEntityChangedError);
          }          
          this.onDestroySub$.next(true);

          return of({ token: 'ERROR', result: null });
        }),
        takeUntil(this.onDestroySub$)
      ).subscribe(
        (rezObj: { token: string; result: any }) => {
          console.log(`\tSUBSCRIBE: EntityCreditScoreInquiryService.creditScoreInquiry().subscribe(NEXT): rezObj: %O`, rezObj);

          if (rezObj.token === 'OK') {
            
            if (stateService) {
              const isEntityChangedExit: IsEntityChangedInterface = {
                op: EntityStateEnum.UPDATE,
                isEnd: true,
                opResult: EntityChangeResultEnum.SUCCESS,
                messageType: EntityMessageTypeEnum.ERROR,
                message: rezObj.result.message
              };
              stateService.next(isEntityChangedExit);
            }
          }
        }
        , err => console.log(`\tSUBSCRIBE: EntityCreditScoreInquiryService.creditScoreInquiry().subscribe(ERROR): %O`, err)
        , () => console.log(`\tSUBSCRIBE: EntityCreditScoreInquiryService.creditScoreInquiry().subscribe(COMPLETE)`)    
      );

    // eom 
  }
  // eoc  EntityCreditScoreInquiryService --------
}
