import { Component, OnInit, OnDestroy, ChangeDetectionStrategy, Input } from '@angular/core';


@Component({
  selector: 'app-loan-symmary',
  templateUrl: 'loan-symmary.component.html',
  styleUrls: ['loan-symmary.component.scss'],
  providers: [],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class LoanSymmaryComponent implements OnInit, OnDestroy {

  @Input() clientData: any;

  summaryColor1 = '#6FA8DC';  // '#6FA8DC'  '#DDBDF1'
  summaryColor2 = '#9FC5F8';  // '#9FC5F8'  '#DDBDF1'
  
  constructor()  {
  }

  ngOnInit() {


  }

  getScore(data: any) {
    let value = '';
    if (data) {
      if (data.score) {
        return value + data.score;
      }  
    }
    return value;
  }

  getSummary(data: any) {
    let value = '';
    if (data) {
      if (data.summary) {
        return value + data.summary;
      }  
    }
    return value;
  } 


  ngOnDestroy() {

  }

}
