import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpErrorResponse, HttpHeaders } from '@angular/common/http';

import { of, Subject } from 'rxjs';
import { map, catchError, takeUntil, shareReplay, startWith, delay } from 'rxjs/operators';

import { EntityStateService } from '../../../shared/services/entity-state.service';
import { IsEntityChangedInterface, EntityMessageTypeEnum, 
  EntityStateEnum, EntityChangeResultEnum } from '../../../shared/model/entity-state.interface';

import {ServerStatusService } from '../../../shared/services/server-status.service';

import { environment } from 'src/environments/environment';

export interface DenyLoanApplicationInterface {
  message: string,
  clientNumber?: any,
  clientState?: any,
  personId?: any,
  clientId?: any
}

export const WrongDenyLoanApplication: DenyLoanApplicationInterface = { 
  message: 'WRONG',
}

@Injectable()
export class DenyLoanApplicationService {
  private apiUrl = environment.apiUrl;

  private curDenyLoanApplicationSub$ = new Subject<any | DenyLoanApplicationInterface>();
  public curDenyLoanApplication$ = this.curDenyLoanApplicationSub$.asObservable()
                        .pipe(
                          startWith(WrongDenyLoanApplication),
                          shareReplay(1)
                        );
  public curDenyLoanApplication: DenyLoanApplicationInterface = WrongDenyLoanApplication;
  
  private onDestroySub$ = new Subject<boolean>();

  constructor(
    private http: HttpClient,
    private serverStatusSrv: ServerStatusService,

  ) { 
    this.curDenyLoanApplication$.subscribe(
      response => {
        this.curDenyLoanApplication = response;
      }
    );
  }
  // ------------------------------------------------------------
  /* */
  public isDenyLoanApplicationWrong(response: DenyLoanApplicationInterface): boolean {
    if (!response) {
      throw new Error('Parameter [response] is null');
    }
    if (response === WrongDenyLoanApplication) {
      return true;
    } else {
        return false;
    }
  }  
  public isCurDenyLoanApplicationCorrect(): boolean {
    return !this.isDenyLoanApplicationWrong(this.curDenyLoanApplication);
  }
  public isCurDenyLoanApplicationWrong(): boolean {
    return !this.isDenyLoanApplicationWrong(this.curDenyLoanApplication);
  }
 
  public clearCurDenyLoanApplication(): void {
    if (this.isCurDenyLoanApplicationCorrect()) {
      this.curDenyLoanApplicationSub$.next(WrongDenyLoanApplication);
    }
  }
 
  public isDenyLoanApplicationNotFound(response: any): boolean {
    if (!response) {
      throw new Error('Parameter [response] is null');
    }
    if (!response.message) {
        throw new Error('Parameter [response] has no [message] fields.')
    } else if (response.message == null) {
        return true;
    } else {
      return false;
    }
  }  
// ------------------------------------------------------------


public denyLoanApplication(clientId: number, loanApplicationId: number, stateService: EntityStateService): void {
   console.log('DenyLoanApplicationService.denyLoanApplication() clientId -> %O, loanApplicationId -> %O ,stateService -> %O', 
                          clientId, loanApplicationId, stateService);

  if (stateService) {
    const isEntityChangedInterface : IsEntityChangedInterface = {
      op: EntityStateEnum.LOAD,
      isEnd: false,
      opResult: EntityChangeResultEnum.NOSET
    };
    stateService.next(isEntityChangedInterface);
  }
  /* put  !!!
        https://pacersnacksacks.com/api/clients/1/loanapplications/1/status
        {"status": 'Deny'}
        // responce:
        {
          "message": "Client loan application status updated.",
          "clientNumber": null,
          "clientState": null
        }
  */
   // Parameter [response] has no [message] or [clientNumber] fields.

  const deny: any = {
     status: 'Deny'
    };

  const fund: any = {
    status: 'Fund'
  };

  this.http.put<any>(`${this.apiUrl}/clients/${clientId}/loanapplications/${loanApplicationId}/status`, deny, {
    headers: new HttpHeaders() 
      //.set("Content-Type", "application/json")  // !!! work with only "Content-Type", "application/json" ???
      .set("Accept", "application/json")
  })
  .pipe(
    map((response: any) => {
       console.log('\t PIPE DenyLoanApplicationService.denyLoanApplication().map(ANY) -> %O', response);
      
      if (this.isDenyLoanApplicationNotFound(response)) {
        throw new Error(`Loan Application with [clientId]=${clientId} [loanApplicationId]=${loanApplicationId} has not found.`);
      }

      return {token: 'OK', 'message': response.message};
    }),
    catchError((err: any): any =>{
       console.log('\t PIPE DenyLoanApplicationService.denyLoanApplication().catchError() #2: -> %O', err);

      let msg: string;
      let isEntityChangedInterface: IsEntityChangedInterface = null;
      if (stateService) {
        isEntityChangedInterface = {
          op: EntityStateEnum.LOAD,
          isEnd: true,
          opResult: EntityChangeResultEnum.ERROR,
        };
      }
      if (err instanceof HttpErrorResponse) {
        // Server error 
        const status = err.status;
        if (status === 0) {
          msg = `SERVER ERROR: HTTP failure response for ${err.url}`;
        } else if (status > 0 && status < 600) {
            msg = `SERVER ERROR: ${status} - ${this.serverStatusSrv.getStatusText(status.toString())}`;
        }
        isEntityChangedInterface.messageType = EntityMessageTypeEnum.ERROR;
        isEntityChangedInterface.message = msg;
      } else {
        // Client error 
        msg = err.message ? err.message : err.toString(); ;
        isEntityChangedInterface.messageType = EntityMessageTypeEnum.ERROR;
        isEntityChangedInterface.message = `CLIENT AND LOAN APPLICATION ERROR: ${msg}`;
      }
      if (stateService) {
        stateService.next(isEntityChangedInterface);
      }      

      this.clearCurDenyLoanApplication();  // ???
      this.onDestroySub$.next(true);
      
      return of({token: 'ERROR', response: null});
    }),
    // delay(5000),  // test
    takeUntil(this.onDestroySub$)
  ).subscribe(
    (rezObj: {token: string; message: string} ) => {
       console.log(`\t SUBSCRIBE: DenyLoanApplicationService.denyLoanApplication().subscribe(NEXT): rezObj -> %O`, rezObj);

       if (rezObj.token === 'OK') {
        this.curDenyLoanApplicationSub$.next(rezObj.message); // BEFOR stateService.next()!
        if (stateService) {
          const isEntityChangedInterface: IsEntityChangedInterface = {
            op: EntityStateEnum.LOAD,
            isEnd: true,
            opResult: EntityChangeResultEnum.SUCCESS,
            message: rezObj.message
          };
          stateService.next(isEntityChangedInterface);
        }
      }
    }
    // , err => console.log(`\tSUBSCRIBE: DenyLoanApplicationService.denyLoanApplication().subscribe(ERROR): %O`, err)
    // , () => console.log(`\tSUBSCRIBE: DenyLoanApplicationService.denyLoanApplication().subscribe(COMPLETE)`)    
  );

}




  public fundLoanApplication(clientId: number, loanApplicationId: number, stateService: EntityStateService): void {
    console.log('DenyLoanApplicationService.denyLoanApplication() clientId -> %O, loanApplicationId -> %O ,stateService -> %O',
        clientId, loanApplicationId, stateService);

    if (stateService) {
      const isEntityChangedInterface : IsEntityChangedInterface = {
        op: EntityStateEnum.LOAD,
        isEnd: false,
        opResult: EntityChangeResultEnum.NOSET
      };
      stateService.next(isEntityChangedInterface);
    }


    const fund: any = {
      status: 'Fund'
    };

    this.http.put<any>(`${this.apiUrl}/loans/${loanApplicationId}/status`, fund, {
      headers: new HttpHeaders()
      //.set("Content-Type", "application/json")  // !!! work with only "Content-Type", "application/json" ???
          .set("Accept", "application/json")
    })
        .pipe(
            map((response: any) => {
              console.log('\t PIPE DenyLoanApplicationService.denyLoanApplication().map(ANY) -> %O', response);

              if (this.isDenyLoanApplicationNotFound(response)) {
                throw new Error(`Loan Application with [clientId]=${clientId} [loanApplicationId]=${loanApplicationId} has not found.`);
              }

              return {token: 'OK', 'message': response.message};
            }),
            catchError((err: any): any =>{
              console.log('\t PIPE DenyLoanApplicationService.denyLoanApplication().catchError() #2: -> %O', err);

              let msg: string;
              let isEntityChangedInterface: IsEntityChangedInterface = null;
              if (stateService) {
                isEntityChangedInterface = {
                  op: EntityStateEnum.LOAD,
                  isEnd: true,
                  opResult: EntityChangeResultEnum.ERROR,
                };
              }
              if (err instanceof HttpErrorResponse) {
                // Server error
                const status = err.status;
                if (status === 0) {
                  msg = `SERVER ERROR: HTTP failure response for ${err.url}`;
                } else if (status > 0 && status < 600) {
                  msg = `SERVER ERROR: ${status} - ${this.serverStatusSrv.getStatusText(status.toString())}`;
                }
                isEntityChangedInterface.messageType = EntityMessageTypeEnum.ERROR;
                isEntityChangedInterface.message = msg;
              } else {
                // Client error
                msg = err.message ? err.message : err.toString(); ;
                isEntityChangedInterface.messageType = EntityMessageTypeEnum.ERROR;
                isEntityChangedInterface.message = `CLIENT AND LOAN APPLICATION ERROR: ${msg}`;
              }
              if (stateService) {
                stateService.next(isEntityChangedInterface);
              }

              this.clearCurDenyLoanApplication();  // ???
              this.onDestroySub$.next(true);

              return of({token: 'ERROR', response: null});
            }),
            // delay(5000),  // test
            takeUntil(this.onDestroySub$)
        ).subscribe(
        (rezObj: {token: string; message: string} ) => {
          console.log(`\t SUBSCRIBE: DenyLoanApplicationService.denyLoanApplication().subscribe(NEXT): rezObj -> %O`, rezObj);

          if (rezObj.token === 'OK') {
            this.curDenyLoanApplicationSub$.next(rezObj.message); // BEFOR stateService.next()!
            if (stateService) {
              const isEntityChangedInterface: IsEntityChangedInterface = {
                op: EntityStateEnum.LOAD,
                isEnd: true,
                opResult: EntityChangeResultEnum.SUCCESS,
                message: rezObj.message
              };
              stateService.next(isEntityChangedInterface);
            }
          }
        }
        // , err => console.log(`\tSUBSCRIBE: DenyLoanApplicationService.denyLoanApplication().subscribe(ERROR): %O`, err)
        // , () => console.log(`\tSUBSCRIBE: DenyLoanApplicationService.denyLoanApplication().subscribe(COMPLETE)`)
    );

  }


// end
}
