import { Component, OnInit, OnDestroy, ChangeDetectionStrategy, Input } from '@angular/core';


@Component({
  selector: 'app-client-detail-avatar',
  templateUrl: 'client-detail-avatar.component.html',
  styleUrls: ['client-detail-avatar.component.scss'],
  providers: [],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ClientDetailAvatarComponent implements OnInit, OnDestroy {

  @Input() isVisible = false;

  @Input() clientId: number;

  // @Input() profileImageUrl: string;
  @Input() firstName: string;
  @Input() lastName: string;
  

  @Input() workPhoneNumber: string;
  @Input() emailAddress: string;

  constructor()  {
  }

  ngOnInit() {

  }

  getFullName(firstName: string, lastName: string) {
    return `${firstName} ${lastName}`;
  }

  getPhoneNumber(value: string) {

    if (!value || value === '') {
      return  '-';
    }
    return value;
  }

  getEmail(value: string) {

    if (!value || value === '') {
      return  '-';
    }
    return value;
  }

  isExist(valie: string) {

    if (!valie || valie === '') {
      return false;
    }
    return true;
 }


  isProfileImage(profileImage: string) {

     if (!profileImage || profileImage === '') {
       return false;
     }
     return true;
  }

  getProfileImageUrl(profileImage: string) {
    return profileImage;
  }

  ngOnDestroy() {
  }

}
