import { Component, OnInit, OnDestroy, ChangeDetectionStrategy, Input, Inject } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';

import { Observable, Subject } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import {Subscription} from 'rxjs';

import { Router, ActivatedRoute, Params, ParamMap } from '@angular/router';

// -----------

import { MatSnackBar, SimpleSnackBar, MatSnackBarRef } from '@angular/material';

import { UserWithTokenInterface } from '../../../auth/user.interface';

import {
  IsEntityChangedInterface, EntityMessageTypeEnum, EntityStateEnum,
  EntityChangeResultEnum, OpStateInterface
}
  from '../../../shared/model/entity-state.interface';

import { EntityStateService } from '../../../shared/services/entity-state.service';
import { UserService } from '../../../auth/user.service';

import { EntityCreditScoreInquiryService } from './../../../shared/services/entity-credit-score-inquiry.service';

// -----------
import { ClientDetailService } from './../services/client-detail.service';
// import { ClientDetailOverviewService } from './services/client-detail-overview.service';

import { ClientDetailAddressInterface, ClientDetailInterface } from './../client-detail.interface';

@Component({
  selector: 'app-bankstatement-inquiry',
  templateUrl: 'bankstatement-inquiry.component.html',
  styleUrls: ['bankstatement-inquiry.component.scss'],
  providers: [
    EntityStateService,
    { provide: 'LOAN_APP_STATE_SERVICE', useClass: EntityStateService}
  ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class BankStatementInquiryComponent implements OnInit, OnDestroy {

    @Input() bankStatement: any;

  creditScoreParamsFG: FormGroup;

  public currentUser$: Observable<UserWithTokenInterface> = null;
  public currentClientDetail$: Observable<ClientDetailInterface> = null;
    public isDenyChanged$: Observable<OpStateInterface> = null;
  currentClientDetail: any;

  // --------------------------------------------------------------
  private simpleSnackBarRef: MatSnackBarRef<SimpleSnackBar> = null;
  public isClientDetailOverviewChanged$: Observable<OpStateInterface> = null;

  public isInquiryChanged$: Observable<OpStateInterface> = null;
  // --------------------------------------------------------------
    //

   
  constructor( private activatedRoute: ActivatedRoute,
               private router: Router,
               private formBuilder: FormBuilder,

               protected userSrv: UserService,
               protected clientDetailOverviewStateSrv: EntityStateService,
               protected entityCreditScoreInquiryService : EntityCreditScoreInquiryService,
               protected clientDetailOverviewService : ClientDetailService,
               private snackBarSrv: MatSnackBar,
               @Inject('LOAN_APP_STATE_SERVICE') protected inquiryStateSrv: EntityStateService ) 
               {
                inquiryStateSrv.context = 'Inquiry Update Srv';
  }

  ngOnInit() {

    this.currentUser$ = this.userSrv.curUser$
    .pipe(
      tap((user: UserWithTokenInterface) => {
        if (this.userSrv.isAnonymUser(user)) {
          this.clientDetailOverviewService.clearClientDetail();
        }
      })
    );
     // ---------------------

     this.currentClientDetail$ = this.clientDetailOverviewService.curClientDetail$
     .pipe(
        map((client: any) => {
          if (!this.clientDetailOverviewService.isClientDetailNotEmpty(client)) {
            return null;
          }
          this.currentClientDetail = client;
          return client;
        })
     );
          

  }



    ngOnDestroy() {

    }





}
