import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpErrorResponse, HttpHeaders } from '@angular/common/http';

import { of, Subject } from 'rxjs';
import { map, catchError, takeUntil, shareReplay, startWith } from 'rxjs/operators';

import { ClientInterface, WrongClientList } from './client.interface';

import { EntityStateService } from '../shared/services/entity-state.service';
import { IsEntityChangedInterface, EntityMessageTypeEnum, 
  EntityStateEnum, EntityChangeResultEnum } from '../shared/model/entity-state.interface';

import {ServerStatusService } from '../shared/services/server-status.service';

import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ClientListService {
  private apiUrl = environment.apiUrl;

  private curClientListSub$ = new Subject<any[] | ClientInterface[]>();
  public curClientList$ = this.curClientListSub$.asObservable()
                        .pipe(
                          startWith(WrongClientList),
                          shareReplay(1)
                        );
  public curClientList: ClientInterface[] = WrongClientList;
  
  private onDestroySub$ = new Subject<boolean>();

  constructor(
    private http: HttpClient,
    private serverStatusSrv: ServerStatusService,
    ) {
      this.curClientList$.subscribe(
        clients => {
          this.curClientList = clients;
        }
      );
  }
  // ------------------------------------------------------------
  public isWrongClientList(clients: ClientInterface[]): boolean {
    if (!clients) {
      throw new Error('Parameter [clients] is null');
    }
    if (clients === WrongClientList) {
      return true;
    } else {
        return false;
    }
  }  
  public isClientListNotEmpty(clients: ClientInterface[]): boolean {
    if (!clients) {
      throw new Error('Parameter [clients] is null');
    }
    if (clients === WrongClientList) {
      return false;
    } else {
        return true;
    }
  }
  private isClientListEmpty(): boolean {
    return !this.isClientListNotEmpty(this.curClientList);
  }
  public clearClientList(): void {
    if (!this.isClientListEmpty()) {
      this.curClientListSub$.next(WrongClientList);
    }
  }
// ------------------------------------------------------------
public loadClientList(stateService: EntityStateService): void {
  // console.log('SYNC ENTER ClientListService.loadClientList() %O', stateService);

  if (stateService) {
    const isClentsChangedEnter: IsEntityChangedInterface = {
      op: EntityStateEnum.LOAD,
      isEnd: false,
      opResult: EntityChangeResultEnum.NOSET
    };
    stateService.next(isClentsChangedEnter);
  }
  this.http.get<any>(`${this.apiUrl}/clients`, {
    headers: new HttpHeaders()
      // .set("Content-Type", "application/json")
      .set("Accept", "application/json")
  })
  .pipe(
    map((clients: any) => {
      // console.log('\tASYNC PIPE ClientListService.loadClientList().map(ANY): %O', clients);
      if (this.isWrongClientList(clients)) {
        return null;
      }
      if (!Array.isArray(clients) || clients.length === 0) {            
        return null;
      }  
      let curClientList = clients.map(item => {
        const client0 = {
          clientId: item.id.toString(),
          clientState: item.status,
          creditOfficer: item.loanOfficer,
        }
        let client1, client2: any;         
        if(!item.profile) {
          client1 = {
            firstName: '',
            lastName: '',
            phoneNumber: '',
          }
        } else {
            client1 = {
              firstName: item.profile.firstName,
              lastName: item.profile.lastName,
              email: item.profile.emailAddress,
              gender: item.profile.gender,
              phoneNumber: item.profile.mobilePhoneNumber,
            };
            if (item.profile && item.profile.addresses && 
                Array.isArray(item.profile.addresses) && 
                item.profile.addresses.length > 0) {
                  client2 = {
                    state: item.profile.addresses[0].state,
                    city: item.profile.addresses[0].city,
                  };
            } else {
              client2 = {
                state: '',
                city: '',
              };
            }
          }        
        return Object.assign(client0, client1, client2);
      })

      return {token: 'OK', clients: curClientList};
    }),
    catchError((err: any): any =>{
      // console.log('\tASYNC PIPE ClientListService.loadClientList().catchError() #2: %O', err);

      let msg: string;
      let isClientsChangedError: IsEntityChangedInterface = null;
      if (stateService) {
        isClientsChangedError = {
          op: EntityStateEnum.LOAD,
          isEnd: true,
          opResult: EntityChangeResultEnum.ERROR,
        };
      }
      if (err instanceof HttpErrorResponse) {
        // Server error 
        const status = err.status;
        if (status === 0) {
          msg = `SERVER ERROR: HTTP failure response for ${err.url}`;
        } else if (status > 0 && status < 600) {
            msg = `SERVER ERROR: ${status} - ${this.serverStatusSrv.getStatusText(status.toString())}`;
        }
        isClientsChangedError.messageType = EntityMessageTypeEnum.ERROR;
        isClientsChangedError.message = msg;
      } else {
        // Client error 
        msg = err.message ? err.message : err.toString(); ;
        isClientsChangedError.messageType = EntityMessageTypeEnum.ERROR;
        isClientsChangedError.message = `CLIENT ERROR: ${msg}`;
      }
      if (stateService) {
        stateService.next(isClientsChangedError);
      }      
      this.clearClientList();
      this.onDestroySub$.next(true);
      
      return of({token: 'ERROR', clients: null});
    }),
    takeUntil(this.onDestroySub$)
  ).subscribe(
    (rezObj: {token: string; clients: any[]} ) => {
      // console.log(`\tSUBSCRIBE: ClientListService.loadClientList().subscribe(NEXT): rezObj: %O`, rezObj);

      // if (rezObj.token === 'ERROR') {
      //   if (stateService) {
      //     const isClientsChangedError: IsEntityChangedInterface = {
      //       op: EntityStateEnum.LOAD,
      //       isEnd: true,
      //       opResult: EntityChangeResultEnum.ERROR,
      //     };
      //     stateService.next(isClientsChangedError);
      //   }
      //   this.clearClientList();
      // } else
       if (rezObj.token === 'OK') {
        this.curClientListSub$.next(rezObj.clients); // BEFOR stateService.next()!
        if (stateService) {
          const isClientsChangedExit: IsEntityChangedInterface = {
            op: EntityStateEnum.LOAD,
            isEnd: true,
            opResult: EntityChangeResultEnum.SUCCESS
          };
          stateService.next(isClientsChangedExit);
        }
        // this.curClientListSub$.next(rezObj.clients);
      }
    }
    // , err => console.log(`\tSUBSCRIBE: ClientListService.loadClientList().subscribe(ERROR): %O`, err)
    // , () => console.log(`\tSUBSCRIBE: ClientListService.loadClientList().subscribe(COMPLETE)`)    
  );

}

}
