import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../shared/guard/auth.guard';

import { ClientListComponent } from './client-list/client-list.component';
import { ClientDetailComponent } from './client-detail/client-detail.component';

import { ClientDetailOverviewComponent} from './client-detail/client-detail-overview.component';
import { ClientDetailFinancialsComponent} from './client-detail/client-detail-financials.component';
import { ClientDetailDocumemtsComponent} from './client-detail/client-detail-documemts.component';
import { ClientDetailLoansComponent} from './client-detail/client-detail-loans.component';
import { ClientDetailLoanApplicationsComponent} from './client-detail/client-detail-loan-applications.component';

import { ApprovePersonalLoanAppComponent } from './client-detail/approve-personal-loan-app/approve-personal-loan-app.component';
import { FundPersonalLoanComponent } from './client-detail/fund-personal-loan/fund-personal-loan.component';
import { ApproveBuisnessLoanAppComponent } from './client-detail/approve-buisness-loan-app/approve-buisness-loan-app.component';
import { FundBuisnessLoanComponent } from './client-detail/fund-buisness-loan/fund-buisness-loan.component';
import { CreditScoreInquiryComponent } from './client-detail/credit-score-inquiry/credit-score-inquiry.component';

import { ClientDetailIdentificationComponent} from './client-detail/identifications/identification/client-detail-identification.component';

const routes: Routes = [
  {
    path: '',
    component: ClientListComponent,
    canActivate: [AuthGuard],
  },
  {
    path: ':id',
    component: ClientDetailComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: 'overview',
        component: ClientDetailOverviewComponent,
        canActivate: [AuthGuard]
      },
      {           
        path: 'financials',
        component: ClientDetailFinancialsComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'documemts',
        component: ClientDetailDocumemtsComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'loans',
        component: ClientDetailLoansComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'loanapplications',
        component: ClientDetailLoanApplicationsComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'loanapplications/:prodId/personal-loan-app',
        component: ApprovePersonalLoanAppComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'loanapplications/:prodId/buisness-loan-app',
        component: ApproveBuisnessLoanAppComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'financials/creditscoreinquiry',
        component: CreditScoreInquiryComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'overview/identifications/:identificationId',
        component: ClientDetailIdentificationComponent,
        canActivate: [AuthGuard]
       } 
    ]
  }
];

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ],
  exports: [RouterModule],
})
export class ClientsRoutingModule { }
